import React, { useEffect, useState, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getDatabase, ref, get, update } from "firebase/database";
import { getStorage, ref as storageRef, uploadBytes, deleteObject, getDownloadURL } from "firebase/storage";
import { AuthContext } from "./AuthContext ";

const Edit = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState(null);
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [category, setCategory] = useState("general");
  const [file, setFile] = useState(null);

  const db = getDatabase();
  const storage = getStorage();
  const { currentUser } = useContext(AuthContext);

  useEffect(() => {
    const fetchPost = async () => {
      const postRef = ref(db, "posts/" + id);
      const snapshot = await get(postRef);
      if (snapshot.exists()) {
        const data = snapshot.val();
        setPost(data);
        setTitle(data.title);
        setContent(data.content);
        setCategory(data.category);
      } else {
        console.error("   .");
      }
    };
    fetchPost();
  }, [id]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!title || !content) {
      alert("  .");
      return;
    }

    let fileURL = post?.fileURL; //   URL 

    if (file) {
      //     
      if (fileURL) {
        const oldFileRef = storageRef(storage, fileURL);
        await deleteObject(oldFileRef);
      }

      //   
      const newFileRef = storageRef(storage, `uploads/${file.name}`);
      await uploadBytes(newFileRef, file);
      fileURL = await getDownloadURL(newFileRef); //   URL 
    }

    const updatedPost = {
      title,
      content,
      category,
      fileURL: fileURL || null,
      updatedAt: Date.now(),
      editTime: new Date().toLocaleString(),
    };

    const postRef = ref(db, "posts/" + id);
    await update(postRef, updatedPost);

    alert(" !");
    navigate(`/list/${id}`); //      
  };

  if (!post) {
    return <p>  ...</p>;
  }

  return (
    <div style={{ maxWidth: "600px", margin: "0 auto" }}>
      <h1> </h1>
      {currentUser ?
      <form onSubmit={handleSubmit}>
        <div>
          <label>
            :
            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
            />
          </label>
        </div>
        <div>
          <label>
            :
            <textarea
              value={content}
              onChange={(e) => setContent(e.target.value)}
              required
            />
          </label>
        </div>
        <div>
          <label>
            :
            <select value={category} onChange={(e) => setCategory(e.target.value)}>
              <option value="general"></option>
              <option value="react">React</option>
              <option value="react-native">React Native</option>
              <option value="another"></option>
            </select>
          </label>
        </div>
        <div>
          <label>
              (     .):
            <input type="file" onChange={(e) => setFile(e.target.files[0])} />
          </label>
        </div>
        {post.fileURL && (
          <div>
            <p> : <a href={post.fileURL} target="_blank" rel="noopener noreferrer"> </a></p>
          </div>
        )}
        <button type="submit"> </button>
      </form>
      :
      <div>
        <h3 style={{color: 'red'}}>!     !</h3>
      </div>
      }
    </div>
  );
};

export default Edit;