import React, { useState, useContext } from "react";
import { auth } from "./firebaseConfig";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { AuthContext } from "./AuthContext ";

const SignUp = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [adminPassword, setAdminPassword] = useState("");
  const [check, setCheck] = useState(false);

  const { currentUser } = useContext(AuthContext);

  const correctAdminPassword = process.env.REACT_APP_ADMIN_PASSWORD;

  const handleSignUp = async () => {
    try {
      await createUserWithEmailAndPassword(auth, email, password);
      alert(" !");
    } catch (error) {
      console.error(error);
      alert(" !" + error.message);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    handleSignUp();
  };

  const adminCheck = (e) => {
    e.preventDefault();
    adminPassword == correctAdminPassword ? alert(' !') : alert(' !')
    adminPassword == correctAdminPassword ? setCheck(true) : setCheck(false);
  };

  return (
    <div>
    { 
    currentUser ?
    <div>
      <h2>  </h2>
      <h3 style={{color: 'red'}}>!   !</h3>
    </div>
    :
    (
    check ?
      <form onSubmit={handleSubmit}>
      <h2></h2>
      <input type="email" placeholder="" value={email} onChange={(e) => setEmail(e.target.value)} /> <br />
      <input type="password" placeholder="" onChange={(e) => setPassword(e.target.value)} /> <br />
      <button type="submit"></button>
      </form>
      :
      <form onSubmit={adminCheck}>
      <h2> </h2>
      <input type="password" onChange={(e) => setAdminPassword(e.target.value)}></input>
      <button type="submit"></button>
      </form>
    )
    }
    </div>
  );
};

export default SignUp;
