import React, { useEffect, useState, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getDatabase, ref, get, set, push, remove } from "firebase/database";
import { getStorage, ref as storageRef, deleteObject } from "firebase/storage";
import { AuthContext } from "./AuthContext ";

const Detail = () => {
  const { id } = useParams(); // URL ID 
  const [post, setPost] = useState(null);
  const [comments, setComments] = useState([]); //   
  const [nickname, setNickname] = useState("");
  const [password, setPassword] = useState("");
  const [comment, setComment] = useState("");
  
  const navigate = useNavigate(); //   List    
  const db = getDatabase(); // Realtime Database 
  const { currentUser } = useContext(AuthContext); //    

  //   
  useEffect(() => {
    const fetchPost = async () => {
      const postRef = ref(db, "posts/" + id);
      const snapshot = await get(postRef);
      if (snapshot.exists()) {
        setPost(snapshot.val());
      } else {
        console.error("   .");
      }
    };

    const fetchComments = async () => {
      const commentsRef = ref(db, `comments/${id}`); //   
      const snapshot = await get(commentsRef);
      if (snapshot.exists()) {
        const data = snapshot.val();
        setComments(Object.entries(data).map(([key, value]) => ({ id: key, ...value })));
      }
    };

    fetchPost();
    fetchComments();
  }, [id]);

  //   
  const handleDelete = async () => {
    const storage = getStorage();

    if (window.confirm(" ?")) {
      try {
        const postRef = ref(db, "posts/" + id); // Database  
        const fileURL = post?.fileURL; // Storage   
  
        // Realtime Database  
        await remove(postRef);
  
        // Storage  
        if (fileURL) {
          const fileRef = storageRef(storage, fileURL); //  Storage  
          await deleteObject(fileRef); //  
        }
  
        alert(" !");
        navigate("/list");
      } catch (error) {
        console.error("    :", error);
        alert("  .");
      }
    }
  };

  //   
  const handleEdit = () => {
    navigate(`/edit/${id}`); //   
  };

  //   
  const handleAddComment = async () => {
    if (!nickname || !password || !comment) {
      alert(", ,    .");
      return;
    }

    const newComment = {
      nickname,
      password, //      (   )
      content: comment,
      timestamp: Date.now(),
    };

    const commentsRef = ref(db, `comments/${id}`);
    const newCommentRef = push(commentsRef); //  ID   
    await set(newCommentRef, newComment);

    setNickname("");
    setPassword("");
    setComment("");

    alert(" !");
    //    
    setComments((prev) => [...prev, { id: newCommentRef.key, ...newComment }]);
  };

  //   
  const handleDeleteComment = async (commentId, commentPassword) => {
    const userPassword = prompt(" .");
    if (userPassword === commentPassword) {
      const commentRef = ref(db, `comments/${id}/${commentId}`);
      await remove(commentRef);

      alert(" !");
      setComments((prev) => prev.filter((c) => c.id !== commentId));
    } else {
      alert("  .");
    }
  };

  if (!post) {
    return <p>    .</p>;
  }

  return (
    <div>
      <h1>{post.title}</h1>
      <p>{post.content}</p>
      <p>  : {post.uploadTime}</p>
      {(post.editTime) && <p> : {post.editTime}</p>}
      
      {post.fileURL && (
        <div>
          <a href={post.fileURL} target="_blank" rel="noopener noreferrer">
             
          </a>
        </div>
      )}

      <p>: {post.category}</p>

      {/*      */}
      {
        currentUser ? //    
        <div>
          <button onClick={handleEdit}></button>
          <button onClick={handleDelete} style={{ marginLeft: "10px", color: "red" }}>
            
          </button>
        </div>
        :
        ''
      }
      <button onClick={() => {navigate('/list')}}>  </button>

      {/* ====================================== */}
      <hr />
      <h2></h2>

      {/*   */}
      <div>
        {comments.map((c) => (
          <div key={c.id} style={{ border: "1px solid #ddd", padding: "10px", marginBottom: "10px" }}>
            <p>
              <strong>{c.nickname}</strong> ({new Date(c.timestamp).toLocaleString()})
            </p>
            <p>{c.content}</p>
            <button
              style={{ color: "red" }}
              onClick={() => handleDeleteComment(c.id, c.password)}
            >
               
            </button>
          </div>
        ))}
      </div>

      {/*   */}
      <div>
        <input
          type="text"
          placeholder=""
          value={nickname}
          onChange={(e) => setNickname(e.target.value)}
        />
        <input
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <textarea
          placeholder="  ."
          value={comment}
          onChange={(e) => setComment(e.target.value)}
        />
        <button onClick={handleAddComment}> </button>
      </div>

    </div>
  );
};

export default Detail;
