import React, { useState, useContext } from 'react';
import { BrowserRouter, Routes, Route, Link, useNavigate } from 'react-router-dom'; //    
import styled from 'styled-components';
import { AuthContext } from './firebase/AuthContext ';

const Home = () => {
    const [MyText, setMyText] = useState(0);
    const navigate = useNavigate();

    const { currentUser } = useContext(AuthContext); //  
    // console.log(currentUser);
    // console.log(currentUser.email);

    const NumberPlus = () => {
    setMyText(MyText + 1);
    // alert(' 1 !');
    };

    const NumberMinus = () => {
    setMyText(MyText - 1);
    };

    const islogin = () => {
        if (!currentUser) {
            return <div>  .</div>;
        } else if (currentUser) {
            return <div>  .</div>
        } else {
            return <div>.</div>
        };
    };

    return (
        <div style={{backgroundColor:'white', }}>
            <p>   (Home.js)</p>
            {/* <h3> (1228 )</h3>
            <p>
                    ,   , 
                <br />
                     (HTML),  Home Portfolio ,
                <br />
                  (, , )  
            </p> */}
            <div style={{flexDirection:'row'}}>
                <NumberButton onClick={NumberPlus} >  + </NumberButton>
                <NumberButton onClick={NumberMinus} >  - </NumberButton>
            </div>
            <span style={{fontSize: 50, fontWeight:'bold'}}>{MyText}</span>

            {/* <div style={{flexDirection:'row'}}>
                <Link to='/post' style={{margin:5}}> Post  </Link>
                <Link to='/list' style={{margin:5}}> List  </Link>
                <br/>
                <Link to='/signup' style={{margin:5}}> SignUp  </Link>
                <Link to='/login' style={{margin:5}}> Login  </Link>
            </div> */}

            <br/>
            
            <div>
                <button onClick={() => {navigate('/test1', {state : {s1: 'a', s2: 'b'}})}} style={{fontSize: 20}}> test1  </button>
                <button onClick={() => {navigate('/test2')}} style={{fontSize: 20}}> test2  </button>
            </div>
            {islogin()}
        </div>
    );
};

export default Home;

const NumberButton = styled.button`
  padding:5px 10px 5px 10px;
  min-width: 50px;
  height: auto;
  border-radius: 5px;
  font-size: 20px;
  font-weight: 700;
  background-color:#5877f9;
  border:2px solid #5877f9;
  color:#ffffff;
  margin-right: 2px;
  margin-left: 2px;
  cursor: pointer;
`;